module.exports = " <section-cover _v-75a39e68=\"\"></section-cover> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <countdown class=countdown-top _v-75a39e68=\"\"></countdown> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <zone type=one-zone _v-75a39e68=\"\"></zone> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">A Cosmos <a href=# _v-75a39e68=\"\">zone</a> is a distributed ledger (blockchain). Each zone can have differing transaction logic and policies.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <zone type=two-zones _v-75a39e68=\"\"></zone> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">We’ve developed a trick that allows zones to communicate with each other directly. It’s based on classical BFT algorithms like Tendermint.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <div class=\"gfx-wrapper square\" _v-75a39e68=\"\"> <cosmos-hub hub-name=\"Cosmos Hub\" :spokes=evmSpokes _v-75a39e68=\"\"></cosmos-hub> </div> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">With this technique, we could make Ethereum scale by constructing a common hub blockchain. All inter-zone token movements go through the hub.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <zone type=three-zones _v-75a39e68=\"\"></zone> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">Upgrading the EVM would be seamless, with less risk of contentious hard-forks. Anyone could plug in a better smart contract system.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <div class=\"gfx-wrapper square\" _v-75a39e68=\"\"> <cosmos-hub hub-name=\"Cosmos Hub\" :spokes=distSpokes _v-75a39e68=\"\"></cosmos-hub> </div> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">We could import other blockchains and have a distributed exchange on its own zone.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <div class=\"gfx-wrapper height-seventy\" _v-75a39e68=\"\"> <multiple-hubs _v-75a39e68=\"\"></multiple-hubs> </div> <div class=section-content _v-75a39e68=\"\"> <p _v-75a39e68=\"\">The Cosmos hub isn’t the center of the universe. Any zone can be a hub.</p> </div> </div></section> <section class=section-default _v-75a39e68=\"\"><div class=section-container _v-75a39e68=\"\"> <countdown _v-75a39e68=\"\"></countdown> </div></section> ";


